@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,400;0,700;0,900;1,400&display=swap);
.pd-link{
    text-decoration: none;
    color: inherit;
    transition: .3s ease-in-out;
}

.pd-link:focus, .pd-link:hover, .pd-link:visited, .pd-link:link, .pd-link:active {
    text-decoration: none;
    color: inherit;
}

.pd-link:hover{
    transform: translateY(-3px);
}

@media screen and (max-width: 767px){
    .product-img{
      margin-bottom: 1rem;
    }
} 
.cart-list .list-group-item + .list-group-item, 
.wishlist-list .list-group-item + .list-group-item{
  border: 1px solid rgba(0, 0, 0, 0.125) !important;
}

.cart-remove{
  color: #fb641c;
  cursor: pointer;
}

@media screen and (max-width: 767px){
  .cart-img{
    margin-bottom: 1rem;
  }
} 
.myBtn{
    display: inline-block;
    background: #fb641c;
    color: #fff;
    border-radius: 2px;
    box-shadow: 0px 8px 6px rgba(0,0,0,.1);
    margin-right: 10px;
    text-transform: uppercase;
    padding: 18px 50px;
    transition: .3s ease-in-out;
    cursor:pointer;
}

.myBtn:hover{
    text-decoration: none;
    box-shadow: 0px 2px 5px rgba(0,0,0,.1);
    color:#fff;
}

@media screen and (max-width: 410px){
    .myBtn{
        font-size: 14px;
        padding: 12px 30px;
    }
}
  
@media screen and (max-width: 767px){
    .btn-container{
      display: flex;
      justify-content: center;
      text-align: center;
      margin-bottom: 2rem;
    }
  } 
.counter{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 10px 0;
  }
  
  .counter-value{
    height: 43px;
    width: 60px;
    padding: 0 0 0 8px;
  }
  
  .counter-inc, .counter-dec{
    display: inline-block;
    background: #ff9f00;
    color: #fff;
    font-weight: bold;
    padding: 10px 18px;
    cursor: pointer;
  }
  
  .counter-dec{
    margin: 0 10px;
  }

@media screen and (max-width: 767px){
  .counter{
    margin-bottom: 1.5rem;
  }
} 
@media screen and (max-width: 767px){
    .checkout-img{
      width: 100%;
    }
  } 
*::selection{
  background: none;
}
body{
  font-family: 'Source Sans Pro';
}

a:hover{
  text-decoration: none;
  color: #fff;
}
